import React, {useState} from 'react';
import {Field, reduxForm} from "redux-form";
import styles from "./FormList.module.css";
import {
    InputOnchangeNumberHor,
    InputOnchangeNumberSecMin,
} from "../helpers/formHoc";
import {fullSec} from "../helpers/HelpFuncJs";
import Audio from "../music/audio";

function Timer(props) {
    const [inpState, setInp] = useState(false);
    const [isOnTimer, setIsOnTimer] = useState(!props.timer.pause);/////////////
    const [play, setPlay] = useState(false);
    const [playBtn, setPlayBtn] = useState(false);

    let hourst = 0;
    let menut = 0;
    let sec = 0;
    if (props.timer.status) {
        if(!playBtn) {
            if (!play) {
                setPlay(true);
            }
        }
        if (isOnTimer) {
            setIsOnTimer(false);
        }
        hourst = Math.floor(props.timer.fullTime / 60 / 60);
        menut = Math.floor((props.timer.fullTime - (hourst * 60 * 60)) / 60);
        sec = Math.floor(props.timer.fullTime - (hourst * 60 * 60 + menut * 60));
    } else {
        hourst = Math.floor(props.timer.time / 60 / 60);
        menut = Math.floor((props.timer.time - (hourst * 60 * 60)) / 60);
        sec = Math.floor(props.timer.time - (hourst * 60 * 60 + menut * 60));
    }

    sec = ((sec + '').length === 1) ? '0' + sec : sec;
    menut = ((menut + '').length === 1) ? '0' + menut : menut;
    hourst = ((hourst + '').length === 1) ? '0' + hourst : hourst;
    const sub = (data) => {
        /*
                    * [`hor${props.id}`]
            [`min${props.id}`]
            [`sec${props.id}`]
        * */
        props.newTimersThink(+data[`hor`] * 60 * 60 + (+data[`min`]) * 60 + (+data[`sec`]), props.id);
        setInp(false);
        setIsOnTimer(true);
        props.setForm(false);
    };
    //(props.timer.) <MIDISounds ref={(ref) => (this.midiSounds = ref)} appElementName="root" instruments={[]} drums={[]} />
    return (
        <div>
            {
                (inpState) ?
                    (
                        <div>
                            <Form id={props.id} onSubmit={sub} initialValues={{[`hor`]: hourst, [`min`]: menut, [`sec`]: sec}}
                                  className={styles.timerForm}/>
                        </div>
                    )
                    :
                    (
                        <div style={{display: 'flex', 'alignitems': 'center',}}>
                            <Audio play={play} autoPlay={props.timer.status}/>
                            <span onClick={() => {
                                if (props.formOn) {
                                    return
                                }
                                props.setForm(true);
                                props.timer.delFunc();
                                setInp(true);
                            }}>
                                {hourst}:{menut}:{sec}
                            </span>
                            {
                                (isOnTimer) ? (
                                    <div onClick={() => {
                                        props.timer.delFunc();
                                        props.pauseTimer(props.id);
                                        setIsOnTimer(false);
                                        setPlay(false);
                                    }} className={styles.payse}>
                                        <span></span>
                                        <span></span>
                                    </div>
                                ) : (
                                    <>
                                        <div onClick={() => {
                                            props.newTimersThink(fullSec(hourst, menut, sec), props.id);
                                            setIsOnTimer(true);
                                            setPlayBtn(false);
                                        }} className={styles.payse}>
                                                <img
                                                    src="data:image/svg+xml;utf8;base64," alt=''/>
                                        </div>
                                        {   (play) ?

                                                <div onClick={()=> {
                                                    setPlay(false);
                                                    setPlayBtn(true);
                                                }
                                                } className={styles.payse+' '+styles.song}>
                                                    <img style={{'width':'100%','height':'85%','marginLeft':'-7px'}} src="data:image/svg+xml;utf8;base64," alt=""/>
                                                    <div className={styles.bresh} style={{'marginLeft':'2px'}}></div>
                                                    <div className={styles.bresh2} style={{'marginLeft':'1px'}}></div>
                                                    <div className={styles.lepTop}></div>
                                                    <div className={styles.lepRight}></div>
                                                    <div className={styles.lepBottom}></div>
                                                </div>
                                             : ''
                                        }
                                    </>
                                )
                            }
                        </div>
                )
                }
                </div>
                );
                }
                const ReduxForm = (props) => {
    /*
    * ${props.id}
${props.id}
${props.id}
*   ${props.id}
    ${props.id}
    ${props.id}
    * */      /*      props.initialValues[`hor${props.id}`]='00';
                    props.initialValues[`min${props.id}`]='00';
                    props.initialValues[`sec${props.id}`]='00';*/
                return (
                <form className={props.className} onSubmit={props.handleSubmit}>
                    <Field autoComplete={'off'} tag='input' component={InputOnchangeNumberHor} name={`hor`} />:
                    <Field autoComplete={'off'} tag='input' component={InputOnchangeNumberSecMin} name={`min`} />:
                    <Field autoComplete={'off'} tag='input' component={InputOnchangeNumberSecMin} name={`sec`} />
                    <button className={styles.BottomTimer}>
                    <img src="data:image/svg+xml;utf8;base64," alt=''/>
                    </button>
                </form>
                )
            };
                export const Form = reduxForm({
                form:'timer'
            })(ReduxForm);


                export default Timer;